.CalculatorPadSection {
  display: grid;
  position: fixed;
  left: 0px;
  top: 62px;
  width: 200px;
  grid-gap: 1px;
  grid-template-columns: repeat(4, 50px);
  grid-template-rows: repeat(5, 50px);
  grid-template-areas:
    "clear clear divide multiply"
    "seven eight nine subtract"
    "four five six add"
    "one two three equals"
    "zero zero decimal equals";
}

#zero {
  grid-area: zero;
}
#one {
  grid-area: one;
}
#two {
  grid-area: two;
}
#three {
  grid-area: three;
}
#four {
  grid-area: four;
}
#five {
  grid-area: five;
}
#six {
  grid-area: six;
}
#seven {
  grid-area: seven;
}
#eight {
  grid-area: eight;
}
#nine {
  grid-area: nine;
}
#decimal {
  grid-area: decimal;
}
#add {
  grid-area: add;
}
#subtract {
  grid-area: subtract;
}
#multiply {
  grid-area: multiply;
}
#divide {
  grid-area: divide;
}
#equals {
  grid-area: equals;
}
#clear {
  grid-area: clear;
}

button {
  text-decoration: none;
  font-weight: bold;
  border: none;
  text-align: center;
  color: aliceblue;
  cursor: pointer;
}

button:hover {
  border: 1.1px solid black;
}

#one button,
#two button,
#three button,
#four button,
#five button,
#six button,
#seven button,
#eight button,
#nine button,
#decimal button {
  width: 50px;
  height: 50px;
  background-color: rgb(82, 80, 80);
}

#add button,
#subtract button,
#multiply button,
#divide button {
  width: 50px;
  height: 50px;
  background-color: rgb(102, 102, 102);
}

#equals button {
  background-color: rgb(0, 68, 102);
  padding: 42px 20px;
}

#clear button {
  background-color: rgb(172, 57, 57);
  padding: 16px 40px;
}

#zero button {
  background-color: rgb(82, 80, 80);
  padding: 16px 46px;
}
